<template>
  <div class="login_container">
    <div class="login_box">
      <!-- 这是头像区域 -->
      <div class="avatar_box">
        <img src="../assets/logo.png" alt />
      </div>
      <!-- 这是登录表单区域 -->
      <el-form ref="loginFormRef" :model="loginForm" :rules="loginFormRules" label-width="0px" class="login_form">
        <!-- 用户名 -->
        <el-form-item prop="username">
          <el-input v-model="loginForm.username" prefix-icon="iconfont icon-user" ></el-input>
        </el-form-item>
        <!-- 密码 -->
        <el-form-item prop="password"> 
          <el-input v-model="loginForm.password" prefix-icon="iconfont icon-3702mima" type="password"></el-input>
        </el-form-item>
        <!-- 按钮区域 -->
        <el-form-item class="btns">    
            <el-button type="primary" @click="login">登录</el-button>
            <el-button type="info" @click="resetLoginForm">重置</el-button>   
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
export default {
    data(){
        return {
            // 这是登录表单的数据绑定对象
            loginForm: {
                username: 'admin',
                password: '123456'
            },
            // 这是表单的规则验证对象
            loginFormRules: {
                // 验证用户名是否合法
                username: [
                    // true为必填项，blur为文本框失去焦点时触发
                    { required: true, message: '请输入登录名称', trigger: 'blur' },
                    { min: 3, max: 10, message: '长度在 3 到 10 个字符', trigger: 'blur' }
                ],
                // 验证密码是否合法
                password: [
                     // true为必填项，blur为文本框失去焦点时触发
                    { required: true, message: '请输入登录密码', trigger: 'blur' },
                    { min: 6, max: 15, message: '长度在 6 到 15 个字符', trigger: 'blur' }
                ]
            }
        }
    },
    methods: {
        // 点击重置按钮，重置登录表单
        resetLoginForm() {
            // console.log(this);
            this.$refs.loginFormRef.resetFields();
        },
        // 点击登录安努，实现登录操作
        login() {
            this.$refs.loginFormRef.validate(async valid => {
                // console.log(valid);
                if(!valid) return;
                // await 只能用在被async修饰方法中
               const { data: res} = await this.$http.post('login', this.loginForm);
                // console.log(result);
                if(res.meta.status !== 200) return this.$message.error('登录失败');
                this.$message.success('登录成功')
                // 1.将登录之后的 token， 保存到客户端的 sessionStorage 中   lockStorage是持久化的存储机制，sessionStorage是会话期间的存储机制
                //   1.1 项目中 除了登录之外的其他 API接口，必须在登录之后才能访问
                //   1.2 token 只应在当前网站打开期间生效，所以将token 保存在 sessionStorage 中  （cookie是存在硬盘，session是窗口关闭销毁）
                // console.log(res);
                window.sessionStorage.setItem('token',res.data.token);
                // 2.通过编程式导航跳转到后台主页，路由地址是 /home
                this.$router.push('/home')
                
                
                
            });
        }
        
    }
};
</script>

