  height: 130px;
  width: 130px;
  border: 3px solid #eee;
  border-radius: 50%;
  padding: 10px;
  box-shadow: 0 0 10px #ddd;
  // 定位  水平居中先移动50的距离，在加个横向的位移距离，按照盒子的大小
  position: absolute;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: #fff;
  img {
    width: 100%;
    height: 100%;
    border-radius: 50%;
    background-color: #eee;
  }
}
.login_form {
    position: absolute;
    bottom: 0;
    width: 100%;
    padding: 0 20px;
    // 让元素不溢出
    box-sizing: border-box;
